<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('GROUP_NAME') }}</label>
          <vs-input class="w-full" v-model="userGroup.description"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('TYPE') }}</label>
          <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="userGroup.groupTypeEnumId">
            <vs-select-item v-for="(enumeration, index) in groupTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
          </vs-select>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full">
          <label class="vs-input--label">{{ $t('IPS_ALLOWED') }}</label>
          <vs-input class="w-full" v-model="userGroup.ipAllowed"/>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="modifyUserGroup('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="modifyUserGroup('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                  <vs-dropdown-item @click="modifyUserGroup('view')">
                    <span>{{ $t('SUBMIT_VIEW') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import SecurityService from '@/services/nucleus/SecurityService'
import EnumerationService from '@/services/commons/EnumerationService'

export default {
  name: 'user-group-modify',
  mixins: [AlertMixin],
  data: function () {
    return {
      userGroup: {},
      groupTypes: [],
      groupTypeEnumId: '',
      ipAllowed: '',
      description: ''
    }
  },
  methods: {
    async load () {
      let self = this
      SecurityService.userGroups({
        userGroupId: this.$route.params.id
      }).then(function (response) {
        self.userGroup = response.data.userGroupList[0] || {}
      })

      EnumerationService.enumerations({
        enumTypeId: 'UserGroupType',
        ignoreEnumId: 'UgtMoquiAdmin,UgtRemoteSystems',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.groupTypes = response.data.enumerationList || []
      })
    },
    modifyUserGroup (nextAction) {
      let self = this
      SecurityService.modifyUserGroup({
        userGroupId: this.userGroup.userGroupId,
        groupTypeEnumId: this.userGroup.groupTypeEnumId,
        ipAllowed: this.userGroup.ipAllowed,
        description: this.userGroup.description
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewUserGroup', params: { id: response.data.userGroupId } })
          } else {
            self.$router.push({ name: 'BrowseUserGroups' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
